import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AdminGuard } from './admin-guard.service';

import { AdminComponent } from './admin.component';
import { AdminHomeComponent } from './admin-home/admin-home.component';
import { NotAuthComponent } from './not-auth/not-auth.component';

const adminRoutes: Routes = [
  {
    path: 'admin',
    data: {
      breadcrumbs: 'Admin'
    },
    canActivate:[AdminGuard],
    canActivateChild: [AdminGuard],
    component: AdminComponent,
    children: [
      {
        path: 'courses',
        loadChildren: function() { return new Promise(function (resolve, reject) {  (require as any).ensure([], function (require: any) {    resolve(require('./course/course.module')['CourseModule']);  }, function() {    reject({ loadChunkError: true });  });}) },
        data: {
          breadcrumbs: 'Courses'
        }
      },
      {
        path: 'students',
        loadChildren: function() { return new Promise(function (resolve, reject) {  (require as any).ensure([], function (require: any) {    resolve(require('./student/student.module')['StudentModule']);  }, function() {    reject({ loadChunkError: true });  });}) },
        data: {
          breadcrumbs: 'Students'
        }
      },
      {
        path: '',
        component: AdminHomeComponent
      }
    ]
  },
  {
    path: 'admin/not-auth',
    component: NotAuthComponent
  }
];

@NgModule({
  imports: [
    RouterModule.forChild(adminRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class AdminRouteModule {}



