import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ScenarioComponent } from './scenario.component';
import { ScenarioResolver } from './scenario.resolver';
import { ListComponent } from './list/list.component';

//import { LocationRoutes } from './location/location.routes';
const ScenarioRoutes: Routes = [
  {
    path: 'scenarios',
    data: {
      breadcrumbs: 'Scenarios'
    },
    component: ScenarioComponent,
    children: [
      {
        path: ':scenId',
        loadChildren: function() { return new Promise(function (resolve, reject) {  (require as any).ensure([], function (require: any) {    resolve(require('./location/location.module')['LocationModule']);  }, function() {    reject({ loadChunkError: true });  });}) },
        resolve: {
          scenario: ScenarioResolver
        },
        data: {
          breadcrumbs: '{{ scenario.label }}'
        }
      },
      {
        path: '',
        component: ListComponent
      }
    ]
  }
];

@NgModule({
  imports: [
    RouterModule.forChild(ScenarioRoutes)
  ],
  exports: [
    RouterModule
  ]
})
export class ScenarioRouteModule {}



